import React, { Component } from "react";
import { render } from "react-dom";
import Calendar from "./calendar";
import { addMonths } from "date-fns";

const config = {
  apiKey: "AIzaSyCG8sNlUxlUQb4_o9OGN15JSmeDKURbzR4",
  calendarId: "vv0uj9uhqrl6j6m0pugu90uo6c@group.calendar.google.com"
};

const styles = {
  fontFamily: "sans-serif",
  textAlign: "center"
};

class App extends Component {
  state = {
    isError: false,
    items: null
  };
  async componentDidMount() {
    const today = new Date();
    const timeMax = addMonths(today, 3);
    const uri = `https://www.googleapis.com/calendar/v3/calendars/${
      config.calendarId
    }/events?key=${
      config.apiKey
    }&timeMin=${today.toISOString()}&timeMax=${timeMax.toISOString()}&singleEvents=true&orderBy=startTime`;

    let response;
    try {
      response = await fetch(uri);
    } catch (r) {
      this.setState({ isError: true });
    }

    if (!response.ok) {
      this.setState({ isError: true });
    } else {
      const result = await response.json();

      this.setState(result);
    }
  }

  render() {
    return <Calendar {...this.state} />;
  }
}

render(<App />, document.getElementById("dsa-cal-app"));



